import * as Questionnaires from '../Questionnaires';
import * as Const from '../Const';

export function GetUsagePeriodOptions () {
  const usagePeriodOptions = [];
  Questionnaires.UsagePeriods.forEach(element => {
    usagePeriodOptions.push({ value: element, label: element });
  });

  return usagePeriodOptions;
}

export function GetPurposeOptions () {
  const purposeOptions = [];
  Questionnaires.Purposes.forEach(element => {
    purposeOptions.push({ value: element, label: element });
  });

  return purposeOptions;
}

export function GetViewedMediaOptions () {
  const viewedMediaOptions = [];
  Questionnaires.ViewedMedias.forEach(element => {
    viewedMediaOptions.push({ value: element, label: element });
  });

  return viewedMediaOptions;
}

export function GetCurrentDate () {
  var dt = new Date();
  return {
    year: dt.getFullYear(),
    month: dt.getMonth() + 1,
    date: dt.getDate()
  };
}

export function GetInitBirthDate () {
  var dt = new Date();
  return {
    year: dt.getFullYear() - 18,
    month: 1,
    date: 1
  };
}

export function IsValidDate (year, month, date) {
  var newDate = new Date();
  newDate.setFullYear(year, month - 1, date);
  if ((newDate.getFullYear() === year) && (newDate.getMonth() + 1 === month) && (newDate.getDate() === date)) {
    return true;
  } else {
    return false;
  }
}

export function GetStartYearItems () {
  var dt = new Date();
  const startYearItems = [];
  startYearItems.push(dt.getFullYear());
  startYearItems.push(dt.getFullYear() + 1);

  return startYearItems;
}

export function GetBirthYearItems () {
  var dt = new Date();
  var currentYear = dt.getFullYear();
  const birthYearItems = [];
  for (var year = currentYear - 80; year <= currentYear - 18; year++) {
    birthYearItems.push(year);
  }

  return birthYearItems;
}

export function GetMonthItems () {
  const monthItems = [];
  for (var month = 1; month <= 12; month++) {
    monthItems.push(month);
  }
  return monthItems;
}

export function GetDayItems () {
  const dayItems = [];
  for (var day = 1; day <= 31; day++) {
    dayItems.push(day);
  }
  return dayItems;
}

export function ValidateInput (data) {
  // console.log(data);
  const errors = [];
  if (data.customerType === Const.CustomerType.Personal) {
    const personalInfo = data.personalInfoItem;
    if (personalInfo.lastName.trim() === '') {
      errors.push('氏名（姓）を入力してください。');
    }
    if (personalInfo.firstName.trim() === '') {
      errors.push('氏名（名）を入力してください。');
    }
    if (personalInfo.lastNameKana.trim() === '') {
      errors.push('フリガナ（セイ）を入力してください。');
    }
    if (personalInfo.firstNameKana.trim() === '') {
      errors.push('フリガナ（メイ）を入力してください。');
    }
    if (personalInfo.email.trim() === '') {
      errors.push('メールアドレスを入力してください。');
    }

    if (personalInfo.isValidEmail === false) {
      errors.push('メールアドレスが正しくありません。');
    }
    if (personalInfo.password.trim() === '') {
      errors.push('マイページログインパスワードを入力してください。');
    }
    if (personalInfo.isValidPassword === false) {
      errors.push('マイページログインパスワードに半角英数7文字以上を入力してください。');
    }

    const mobilePhone = `${personalInfo.mobilePhone1}${personalInfo.mobilePhone2}${personalInfo.mobilePhone3}`;
    const tel = `${personalInfo.tel1}${personalInfo.tel2}${personalInfo.tel3}`;
    if (mobilePhone === '' && tel === '') {
      errors.push('ご連絡先（携帯）またはご連絡先（固定）のどちらかを入力してください。');
    }

    if (mobilePhone !== '' && (personalInfo.mobilePhone1.trim() === '' || personalInfo.mobilePhone2.trim() === '' || personalInfo.mobilePhone3.trim() === '')) {
      errors.push('ご連絡先（携帯）を入力してください。');
    }

    if (tel !== '' && (personalInfo.tel1.trim() === '' || personalInfo.tel2.trim() === '' || personalInfo.tel3.trim() === '')) {
      errors.push('ご連絡先（固定）を入力してください。');
    }

    if (personalInfo.isValidMobilePhone === false) {
      errors.push('ご連絡先（携帯）が正しくありません。');
    }

    if (personalInfo.isValidTel === false) {
      errors.push('ご連絡先（固定）が正しくありません。');
    }

    const fax = `${personalInfo.fax1}${personalInfo.fax2}${personalInfo.fax3}`;
    if (fax !== '' && (personalInfo.fax1 === '' || personalInfo.fax2 === '' || personalInfo.fax3 === '')) {
      errors.push('ご連絡先（FAX）を入力してください。');
    }

    if (personalInfo.isValidFax === false) {
      errors.push('ご連絡先（FAX）が正しくありません。');
    }

    const postalCode = `${personalInfo.postalCode1}${personalInfo.postalCode2}`;
    if (postalCode === '' || personalInfo.postalCode1 === '' || personalInfo.postalCode2 === '') {
      errors.push('郵便番号を入力してください。');
    } else if (personalInfo.isValidPostalCode === false) {
      errors.push('郵便番号が正しくありません。');
    }

    if (personalInfo.address.trim() === '') {
      errors.push('住所を入力してください。');
    }

    if (personalInfo.isValidBirthDate === false) {
      errors.push('生年月日が正しくありません。');
    }

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Relative) {
      if (personalInfo.relativeLastName === '') {
        errors.push('親族のお名前（姓）を入力してください。');
      }

      if (personalInfo.relativeFirstName === '') {
        errors.push('親族のお名前（名）を入力してください。');
      }

      if (personalInfo.relativeLastNameKana === '') {
        errors.push('親族のフリガナ（セイ）を入力してください。');
      }

      if (personalInfo.relativeFirstNameKana === '') {
        errors.push('親族のフリガナ（メイ）を入力してください。');
      }

      const relativePhone = `${personalInfo.relativePhone1}${personalInfo.relativePhone2}${personalInfo.relativePhone3}`;
      if (relativePhone === '' || personalInfo.relativePhone1 === '' || personalInfo.relativePhone2 === '' || personalInfo.relativePhone3 === '') {
        errors.push('親族の電話番号を入力してください。');
      }

      if (personalInfo.isValidRelativePhone === false) {
        errors.push('親族の電話番号が正しくありません。');
      }

      if (personalInfo.relativeRelationship.trim() === '') {
        errors.push('親族の続柄を入力してください。');
      }
    }

    if (personalInfo.additionalContactType === Const.PersonalAdditionalContactType.Company) {
      if (personalInfo.companyName.trim() === '') {
        errors.push('勤務先を入力してください。');
      }

      if (personalInfo.companyNameKana.trim() === '') {
        errors.push('勤務先フリガナを入力してください。');
      }

      const companyPhone = `${personalInfo.companyPhone1}${personalInfo.companyPhone1}${personalInfo.companyPhone1}`;
      if (companyPhone === '' || personalInfo.companyPhone1 === '' || personalInfo.companyPhone2 === '' || personalInfo.companyPhone3 === '') {
        errors.push('勤務先電話番号を入力してください。');
      }

      if (personalInfo.isValidCompanyPhone === false) {
        errors.push('勤務先電話番号が正しくありません。');
      }
    }
  } else if (data.customerType === Const.CustomerType.Company) {
    const companyInfoItem = data.companyInfoItem;

    if (companyInfoItem.companyName.trim() === '') {
      errors.push('社名を入力してください。');
    }

    if (companyInfoItem.companyNameKana.trim() === '') {
      errors.push('社名（フリガナ）を入力してください。');
    }

    if (companyInfoItem.repLastName.trim() === '') {
      errors.push('代表者名（姓）を入力してください。');
    }

    if (companyInfoItem.repFirstName.trim() === '') {
      errors.push('代表者名（名）を入力してください。');
    }

    if (companyInfoItem.repLastNameKana.trim() === '') {
      errors.push('代表者名（フリガナ）（セイ）を入力してください。');
    }

    if (companyInfoItem.repFirstNameKana.trim() === '') {
      errors.push('代表者名（フリガナ）（メイ）を入力してください。');
    }

    if (companyInfoItem.email.trim() === '') {
      errors.push('メールアドレスを入力してください。');
    }

    if (companyInfoItem.isValidEmail === false) {
      errors.push('メールアドレスが正しくありません。');
    }

    if (companyInfoItem.password.trim() === '') {
      errors.push('マイページログインパスワードを入力してください。');
    }

    if (companyInfoItem.isValidPassword === false) {
      errors.push('マイページログインパスワードに半角英数7文字以上を入力してください。');
    }

    const tel = `${companyInfoItem.tel1}${companyInfoItem.tel2}${companyInfoItem.tel3}`;
    if (tel === '' || companyInfoItem.tel1 === '' || companyInfoItem.tel2 === '' || companyInfoItem.tel3 === '') {
      errors.push('ご連絡先（電話番号）を入力してください。');
    }

    if (companyInfoItem.isValidTel === false) {
      errors.push('ご連絡先（電話番号）が正しくありません。');
    }

    const fax = `${companyInfoItem.fax1}${companyInfoItem.fax2}${companyInfoItem.fax3}`;
    if (fax !== '' && (companyInfoItem.fax1 === '' || companyInfoItem.fax2 === '' || companyInfoItem.fax3 === '')) {
      errors.push('ご連絡先（FAX）を入力してください。');
    }

    if (companyInfoItem.isValidFax === false) {
      errors.push('ご連絡先（FAX）が正しくありません。');
    }

    const postalCode = `${companyInfoItem.postalCode1}${companyInfoItem.postalCode2}`;
    if (postalCode === '' || companyInfoItem.postalCode1 === '' || companyInfoItem.postalCode2 === '') {
      errors.push('郵便番号を入力してください。');
    } else if (companyInfoItem.isValidPostalCode === false) {
      errors.push('郵便番号が正しくありません。');
    }

    if (companyInfoItem.address.trim() === '') {
      errors.push('住所を入力してください。');
    }

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.PersonInCharge) {
      if (companyInfoItem.personInChargeLastName.trim() === '') {
        errors.push('担当者お名前（姓）を入力してください。');
      }

      if (companyInfoItem.personInChargeFirstName.trim() === '') {
        errors.push('担当者お名前（名）を入力してください。');
      }

      if (companyInfoItem.personInChargeLastNameKana.trim() === '') {
        errors.push('担当者お名前(フリガナ)（セイ）を入力してください。');
      }

      if (companyInfoItem.personInChargeFirstNameKana.trim() === '') {
        errors.push('担当者お名前(フリガナ)（メイ）を入力してください。');
      }

      const personInChargePhone = `${companyInfoItem.personInChargePhone1}${companyInfoItem.personInChargePhone2}${companyInfoItem.personInChargePhone3}`;
      if (personInChargePhone === '' || companyInfoItem.personInChargePhone1 === '' || companyInfoItem.personInChargePhone2 === '' || companyInfoItem.personInChargePhone3 === '') {
        errors.push('担当者電話番号を入力してください。');
      }

      if (companyInfoItem.isValidPersonInChargePhone === false) {
        errors.push('担当者電話番号が正しくありません。');
      }

      if (companyInfoItem.personInChargeDepartment.trim() === '') {
        errors.push('担当者所属を入力してください。');
      }
    }

    if (companyInfoItem.additionalContactType === Const.CompanyAdditionalContactType.Relative) {
      if (companyInfoItem.relativeLastName.trim() === '') {
        errors.push('代表者のご親族様のお名前（姓）を入力してください。');
      }

      if (companyInfoItem.relativeFirstName.trim() === '') {
        errors.push('代表者のご親族様のお名前（名）を入力してください。');
      }

      if (companyInfoItem.relativeLastNameKana.trim() === '') {
        errors.push('代表者のご親族様のお名前(フリガナ)（セイ）を入力してください。');
      }

      if (companyInfoItem.relativeFirstNameKana.trim() === '') {
        errors.push('代表者のご親族様のお名前(フリガナ)（メイ）を入力してください。');
      }

      const relativePhone = `${companyInfoItem.relativePhone1}${companyInfoItem.relativePhone2}${companyInfoItem.relativePhone3}`;
      if (relativePhone === '' || companyInfoItem.relativePhone1 === '' || companyInfoItem.relativePhone2 === '' || companyInfoItem.relativePhone3 === '') {
        errors.push('代表者のご親族様の電話番号を入力してください。');
      }

      if (companyInfoItem.isValidRelativePhone === false) {
        errors.push('代表者のご親族様の電話番号が正しくありません。');
      }

      if (companyInfoItem.relationship.trim() === '') {
        errors.push('代表者のご親族様の続柄を入力してください。');
      }
    }
  }

  if (data.identification1 === undefined || (data.identification1 !== undefined && data.identification1.size === 0)) {
    errors.push('本人確認書類１をアプロードしてください。');
  }

  const id1FileSize = data.identification1 ? data.identification1.size : 0;
  const id2FileSize = data.identification2 ? data.identification2.size : 0;
  var fileSizeLimitMB = Number(process.env.REACT_APP_FILE_SIZE_LIMIT_MB);
  fileSizeLimitMB = isNaN(fileSizeLimitMB) ? 5 : fileSizeLimitMB;
  if ((id1FileSize + id2FileSize) >= fileSizeLimitMB * 1024 * 1024) {
    errors.push(`本人確認書類のファイルサイズが制限を超えているため、合計${fileSizeLimitMB}MB以下にしてください。`);
  }

  const overallItem = data.overallItem;
  if (overallItem.usagePeriod === '') {
    errors.push('ご利用期間を入力してください。');
  }

  if (overallItem.storageItems.length === 0) {
    errors.push('予定保管物品を入力してください。');
  }

  if (overallItem.purpose === '') {
    errors.push('保管目的を入力してください。');
  }

  if (overallItem.rentalReasons.length === 0) {
    errors.push('当店舗を選んだ理由を教えてくださいを入力してください。');
  }

  return { valid: errors.length === 0, errors };
}

export function GetShopUnitInfo (responseData, shopId, unitTypeId) {
  var shopUnitInfo = {
    shopIdValid: false,
    shopId: undefined,
    shopName: undefined,
    unitTypeIdValid: false,
    unitTypeId: undefined,
    unitTypeName: undefined
  };

  const sites = responseData.sites;
  const shop = sites.find(site => site.id === shopId);

  if (shop === undefined) {
    return shopUnitInfo;
  } else {
    const unittypes = shop.unitTypes;
    const shopName = shop.title.ja;
    const unittype = unittypes.find(ut => ut.id === unitTypeId);

    var retUnitTypeName = '';
    var retUnitTypeId;
    var unitTypeIdValid = false;

    if (unittype !== undefined) {
      unitTypeIdValid = true;
      retUnitTypeName = `${unittype.title.ja} ${unittype.price.toLocaleString()} 円`;
      retUnitTypeId = unittype.id;
    }

    return {
      shopIdValid: true,
      shopId,
      shopName,
      unitTypeIdValid,
      unitTypeId: retUnitTypeId,
      unitTypeName: retUnitTypeName
    };
  }
}
